body {
    font-family: "Segoe UI", Arial, sans-serif;
}

body.example {
    margin-bottom: 50px;
}

/* ### NAV ### */

nav {
    width: 100%;
    line-height: 4rem;
    padding: 0 1.5rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    color: #F5F5F5;
    background-color: #34495e;
}

nav a {
    color: #F5F5F5;
    text-decoration: none;
    margin-left: 1.5rem;
}

nav a:hover {
    text-decoration: underline;
}

#nav-logo {
    margin-left: 0;
    font-size: 1.5rem;
}

#nav-logo:hover {
    text-decoration: none;
}

.container {
    width: 100%;
    max-width: 1120px;
    margin: auto;
    padding: 1.5rem;
}

/* ### CODE TABS ### */

#example-code-area section {
    display: none;
    padding: 1rem;
    border: 1px solid #ccc;
    background-color: #f7f7f7;
}

#example-code-area input {
    display: none;
}

#example-code-area label {
    display: inline-block;
    margin: 0 0 -1px;
    padding: 15px 25px;
    text-align: center;
    border: 1px solid #ccc;
}

#example-code-area label:hover {
    cursor: pointer;
}

#example-code-area input:checked + label {
    border: 1px solid #ccc;
    border-top: 2px solid #34495e;
    border-bottom: 1px solid #f7f7f7;
    background-color: #f7f7f7;
}

#tab1:checked ~ #content1,
#tab2:checked ~ #content2 {
    display: block;
}

.example-code-container {
    border: none !important;
    margin: 0;
}

/* ###   ### */

#example-text-area {
    margin-bottom: 15px;
}

#example-demo-area {
    display: flex;
    flex-direction: column;
    padding-bottom: 10px;
}

/* ### EXAMPLES MAIN PAGE GRID ### */

#demos-container {
    padding: 0.5rem;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
}

#demos-container .demo {
    width: 33.33%;
    min-width: 245px;
    flex-grow: 1;
    padding: 1rem;
    background-color: #ecf0f1;
    border: 0.5rem solid #fff;
    display: block;
    color: #34495e;
    text-decoration: none;
}

#demos-container .demo:hover {
    background-color: #bdc3c7;
    text-decoration: underline;
}

@media (max-width: 752px) {
    #demos-container .demo {
        max-width: 100%;
    }
}

@media (min-width: 752px) {
    #demos-container .demo {
        max-width: 33.33%;
    }
}