﻿
*, *::before, *::after {
    box-sizing: border-box;
}

html, body {
    margin: 0;
    padding: 0;
}

g.axis.y.invisible {
    visibility: hidden;
}

g.axis.x.invisible {
    visibility: hidden;
}


.drawer-parent {
    position: relative;
    overflow: hidden;
}

.drawer-expanded {
    width: 250px !important;
}

.drawer-container {
    background-color: #fff;
    position: absolute;
    top: 0;
    bottom: 0;
    width: 0;
    z-index: 100;
    transition: ease-in width 225ms;
    box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.2);
}

.drawer-container-left {
    left: 0;
}

.sintium-drawer-container-right {
    right: 0;
}

.drawer-content-container {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
}

.drawer-content {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 250px;
    overflow: hidden;
    color: white;
}

.drawer-content-left {
    right: 0;
}

.drawer-content-right {
    left: 0;
}

.drawer-arrow {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 50%;
    height: 90px;
    width: 24px;
    margin-top: -45px;
    color: #fff;
    background-color: #384e58;
    cursor: pointer;
}

.drawer-arrow-left {
    margin-right: -24px;
    right: 0;
    border-radius: 0 5px 5px 0;
}

.drawer-arrow-right {
    left: 0;
    margin-left: -24px;
    border-radius: 5px 0 0 5px;
}

.select-location-switcher {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.select-location-switcher a {
    margin-left: 10px;
}
#goto {
    margin-top: 10px;
    display: flex;
    justify-content: flex-end;
}
.material-icons {
    font-size: 30px !important;
}
.logo {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: -10px;
    margin-left: 10px;
}
.logo i {
    margin-top: 10px;
}

.sintium-temporary-widget-storage {
    display: none;
    overflow: hidden;
}

.widget {
    display: block;
}

.chart-reset-button{
    position: absolute;
    top: 4px;
    right: 4px;
    cursor: pointer;
    color: #555;
}

.chart-reset-button:hover{
    color: #000;
}

.no-labels .pie-label-group {
    display: none;
}

.animated-layer-canvas {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 10000;
    pointer-events: none;
}

/* Collapsible */

.collapsible-container {
    width: 100%;
    user-select: none;
    -moz-user-select: none;
}

.collapsible-entry {
    height: 2.5rem;
    line-height: 2.5rem;
    display: flex;
    border-bottom: 1px solid #dadada;
}

.collapsible-header {
    color: #fff;
    background-color: #34495e;
}

.collapsible-entry.layer-entry {
    color: #000;
    background-color: #fff;
}

.collapsible-entry.setting-entry {
    color: #fff;
    background-color: #494949;
}

.collapsible-body {
    height: 0;
    overflow: hidden;
    transition: height 0.2s ease-out;
    border-bottom: none;
}

.active-collapsible > .collapsible-header .fa-chevron-right {
    transform: rotate(90deg);
}

.collapsible-title {
    padding-left: .5rem;
    flex-grow: 1;
}

.collapsible-header .collapsible-title, .collapsible-header .collapsible-suffix, .collapsible-entry label, .collapsible-entry input {
    cursor: pointer;
}

.layer-entry > .collapsible-title {
    cursor: unset;
}

.collapsible-prefix, .collapsible-suffix {
    flex-grow: 0;
}

.collapsible-prefix {
    padding-left: .5rem;
}

.collapsible-suffix {
    padding-right: 1rem;
    text-align: right;
}
